<template>
	<div class="preview">
		<div class="picture_box">
			<div class="small_img">
				<img :src="pictures[preview_index]" />
				<i class="search"></i>
				<span class="focus" :style="focus_style"></span>
			</div>
			<div class="mask"
			     @mouseenter="show_big_picture()"
			     @mousemove.stop="focus_picutre($event)"
			     @mouseleave="hide_big_picture()"></div>
			<div :class="['big_img',{'active':big_active}]">
				<img :src="pictures[preview_index]" ref="big" :style="big_style" />
			</div>
		</div>
		<div class="list_img">
			<a>
				<i :class="['sprite_prev', {'active': (pictures.length > 5) && (list_index > 0)}]" @click="list_prev"></i>
			</a>
			<a>
				<i :class="['sprite_next', {'active': (pictures.length > 5) && (pictures.length - list_index - 5 > 0)}]" @click="list_next"></i>
			</a>
			<div class="list_center">
				<ul :style="'left:'+ list_index * -58 + 'px;'">
					<li v-for="(picture, index) in pictures" :key="index" :class="{'active':index==preview_index}"
					    @mouseover="switch_picture(index)">
						<img :src="picture" />
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	// 图片列表及详情放大镜(仿京东)
	// 接收参数: src: 列表, 图片 import 进来后的图片地址字符串对象
	// 小图片列表展示, 图片预览, 鼠标悬浮于预览框后显示局部放大图
	// 暂缺列表左右切换功能
	// 暂时只支持正方形的预览框
	// 暂不支持点击显示大图
	export default {
		props: {
			src: {
				default: []
			}
		},
		computed: {
			pictures() {
				return this.src;
			},
			focus_style: function() {
				return {
					top: this.focus_top + "px",
					left: this.focus_left + "px",
				}
			},
			big_style: function() {
				return {
					top: this.big_top + "px",
					left: this.big_left + "px",
					zoom: this.zoom,
				}
			},
		},
		data() {
			return {
				show_detail: false,
				preview_index: 0,
				big_active: false,
				focus_top: -999,
				focus_left: -999,
				FOCUS_SIZE: 160,
				SMALL_SIZE: 350,
				BIG_SIZE: 540,
				big_top: 0,
				big_left: 0,
				zoom: 1,
				list_index: 0,
			}
		},
		methods: {
			switch_picture: function(index) {
				this.preview_index = index;
			},
			show_big_picture: function() {
				this.big_active = true;
			},
			hide_big_picture: function() {
				this.big_active = false;
				this.focus_left = -999;
				this.focus_top = -999;
			},
			focus_picutre: function(e) {
				let WIDTH = e.target.scrollWidth;
				let HEIGHT = e.target.scrollHeight;
				let LEFT = e.target.scrollLeft;
				let TOP = e.target.scrollTop;

				let BIG_WIDTH = this.$refs['big'].width;
				let BIG_HEIGHT = this.$refs['big'].height;
				let BIG_MAX = BIG_WIDTH > BIG_HEIGHT ? BIG_WIDTH : BIG_HEIGHT;
				let black_left = BIG_WIDTH > BIG_HEIGHT ? 0 : (BIG_HEIGHT - BIG_WIDTH) / 2;
				let black_top = BIG_WIDTH > BIG_HEIGHT ? (BIG_WIDTH - BIG_HEIGHT) / 2 : 0;
				let size = this.FOCUS_SIZE + 2; //除了本体尺寸, 还有 1px 的边框

				let left = e.layerX - size / 2;
				if (left < LEFT) left = LEFT;
				else if (left + size > WIDTH) left = WIDTH - size;

				let top = e.layerY - size / 2;
				if (top < TOP) top = TOP;
				else if (top + size > HEIGHT) top = HEIGHT - size;

				this.focus_left = left;
				this.focus_top = top;

				this.big_left = -left / WIDTH * BIG_MAX + black_left;
				this.big_top = -top / HEIGHT * BIG_MAX + black_top;

				this.zoom = (this.BIG_SIZE * this.SMALL_SIZE / this.FOCUS_SIZE) / BIG_MAX;
			},
			list_prev: function() {
				let length = this.pictures.length;
				if (length <= 5 || this.list_index == 0) return;
				if (this.list_index >= 5) this.list_index -= 5;
				else this.list_index = 0;
			},
			list_next: function() {
				let length = this.pictures.length;
				let remain = length - this.list_index - 5;
				if (length <= 5 || remain <= 0) return;
				if (remain >= 5) this.list_index += 5;
				else this.list_index += remain;

			},
		}
	}
</script>

<style scoped>
	.preview {
		--focus_size: 160px;
		width: 352px;
		height: 446px;
	}

	.picture_box {
		position: relative;
	}

	.small_img {
		border: 1px solid #eee;
		position: relative;
		width: 350px;
		height: 350px;
		display: flex;
		align-items: center;
	}

	.small_img>img {
		max-width: 100%;
		max-height: 100%;
		margin: 0 auto;
		border: 1px solid #eee;
	}

	.focus {
		width: var(--focus_size);
		height: var(--focus_size);
		border: 1px solid #aaa;
		background-color: rgba(243, 210, 76, 0.5);
		position: absolute;
		left: -999px;
		top: -999px;
	}

	.search {
		position: absolute;
		right: 0;
		bottom: 0;
		width: 30px;
		height: 30px;
		background: url(../assets/search.png);
	}

	.mask {
		border: 1px solid #eee;
		position: absolute;
		left: 0;
		top: 0;
		width: 350px;
		height: 350px;
		background-color: rgba(243, 210, 76, 0);
		cursor: move;
	}

	.big_img {
		width: 540px;
		height: 540px;
		border: 1px solid #eee;
		overflow: hidden;
		display: none;
		position: absolute;
		top: 0;
		left: 352px;
		background-color: #fff;
	}

	.big_img>img {
		position: absolute;
		border: 1px solid #eee;
	}

	.big_img.active {
		display: block;
	}

	.list_img {
		width: 350px;
		height: 54px;
		margin: 20px 0;
		position: relative;
	}

	.list_img .sprite_prev,
	.list_img .sprite_next {
		width: 22px;
		height: 32px;
		position: absolute;
		top: 11px;
	}

	.list_img .sprite_prev {
		left: 0;
		background: url(../assets/prev-disabled.png);
	}

	.list_img .sprite_prev.active {
		background: url(../assets/prev-active.png);
	}

	.list_img .sprite_prev.active:hover {
		background: url(../assets/prev-hover.png);
		cursor: pointer;
	}

	.list_img .sprite_next {
		right: 0;
		background: url(../assets/next-disabled.png);
	}

	.list_img .sprite_next.active {
		background: url(../assets/next-active.png);
	}

	.list_img .sprite_next.active:hover {
		background: url(../assets/next-hover.png);
		cursor: pointer;
	}

	.list_img .list_center {
		width: 290px;
		height: 54px;
		margin: 0 auto;
		position: relative;
		overflow: hidden;
	}

	.list_img .list_center ul {
		display: flex;
		height: 54px;
		margin: 0;
		padding: 0;
		position: absolute;
	}

	.list_img .list_center ul li {
		width: 50px;
		height: 50px;
		border: 2px solid #fff;
		margin: 0 2px;
		display: flex;
		align-items: center;
	}

	.list_img .list_center ul li.active {
		border: 2px solid #e53e41;
	}

	.list_img .list_center ul li img {
		max-width: 100%;
		max-height: 100%;
	}
</style>



