<template>
	<div :class="['switch-container',{'switch-container-on':value}]" @click="toggle">
		<span :class="['inner',{'inner-on':value}]"></span>
		<span :class="['switch',{'switch-on':value}]"></span>
	</div>
</template>

<script>
	export default {
		props: {
			value: {
				type: Boolean,
				default: false
			}
		},
		// data() {
		// 	return {
		// 		checked: this.value,
		// 	}
		// },
		// watch: {
		// 	checked(value) {
		// 		this.$emit('switch', value)
		// 	}
		// },
		// methods: {
		// 	toggle() {
		// 		this.checked = !this.checked;
		// 	}
		// }
		methods: {
			toggle: function() {
				this.$emit('switch', !this.value);
			}
		}
	}
</script>


<style scoped>
	.switch-container {
		display: block;
		position: relative;
		width: 90px;
		height: 30px;
		border: 1px solid #DFDFDF;
		outline: 0;
		overflow: hidden;
		border-radius: 16px;
		background-color: #DFDFDF;
		transition: background-color 0.1s, border 0.1s;
	}

	.switch-container-on {
		border-color: #558df5;
		background-color: #558df5;
	}

	.inner {
		display: block;
		width: 200%;
		margin-left: -100%;
		transition: all 0.35s cubic-bezier(0.4, 0.4, 0.25, 1.35);
	}

	.inner-on {
		margin-left: 0;
	}

	.inner:before,
	.inner:after {
		display: block;
		float: left;
		width: 50%;
		height: 30px;
		padding: 0;
		line-height: 30px;
		font-size: 19px;
		color: white;
		font-family: Trebuchet, Arial, sans-serif;
		font-weight: bold;
		box-sizing: border-box;
	}

	.inner:before {
		content: "详情";
		padding-left: 13px;
		color: #FFF;
	}

	.inner:after {
		content: "缩略";
		padding-right: 13px;
		color: #555;
		text-align: right;
	}

	.switch {
		display: block;
		width: 30px;
		background: #FFF;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		border-radius: 15px;
		transition: all 0.35s cubic-bezier(0.4, 0.4, 0.25, 1.35);
	}

	.switch-on {
		left: 60px;
		transition: all 0.35s cubic-bezier(0.4, 0.4, 0.25, 1.35);
	}
</style>



