import Vue from 'vue'
import Router from 'vue-router'
import resume from '@/components/resume'

Vue.use(Router)

export default new Router({
	// mode: "history", // 可以用, 但是使用这个模式后本地打开页面是空白的, 必须放到服务器中
	routes: [{
		path: '/',
		name: 'resume',
		component: resume
	}]
})



